<style type="text/css" scoped>
.about-bg {
  width: 100%;
  height: 637px;
  background: url(../assets/topbg.png) no-repeat center center;
  background-size: 100% 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.ag-detail {
  display: flex;
  width: 100%;
  justify-content: space-between;
  align-items: center;
  transition: all 0.5s;
  flex-wrap: wrap;
  padding-left: 10%;
  padding-right: 10%;
}

.ag-item {
  width: calc(100% / 3);
  margin-bottom: 60px;
  text-align: center;
  padding: 0 20px;
  box-sizing: border-box;
  height: 120px;
}

.ag-title {
  color: white;
  font-size: 44px;
  font-weight: bolder;
}

.ag-label {
  color: white;
  font-size: 21px;
  margin-top: 20px;
}


.companyintro-title {
  height: 36px;
  line-height: 36px;
  text-align: center;
  margin-top: 80px;
  margin-bottom: 70px;
}

.companyintro-title .line {
  display: inline-block;
  width: 100px;
  border-top: 2px solid #e0e0e0;

}

.companyintro-title .txt {
  color: black;
  width: 200px;
  vertical-align: -4px;
  font-size: 26px;
  padding-left: 10px;
  padding-right: 10px;
}

.companyintro-title-en {
  height: 24px;
  line-height: 24px;
  color: #acacac;
  font-size: 13px;
}

.companyintro-bg {
  display: flex;
  width: 1200px;
  flex-direction: row;
  margin: 0 auto;
  transition: all 0.5s;
  box-sizing: border-box;
}

.buildingbg {
  background: url(../assets/building.png) no-repeat center center;
  background-size: 100% 100%;
}

.developmentdetail {
  display: flex;
  flex-direction: row;
  width: 1200px;
  /*padding: 0 25px;*/
  box-sizing: border-box;
  margin: 0 auto;
  min-width: 600px;
}

.development-time {
  color: #D9003C;
  font-size: 20px;
  font-weight: bolder;
}

.dp-item {
  display: flex;
  justify-content: center;
  align-items: flex-start;
  height: 93px;
  margin-right: 15px;
  margin-top: -2px;

}

.dp-title-item {
  margin-left: 20px;
  margin-top: -5px;
  height: 98px;
}

.dp-title-item-label {
  font-size: 20px;
  font-weight: 400;
  height: 35px;
}

.dp-title-item-content {
  color: #797979;
}

.moveToTop {
  padding-top: 0;
  opacity: 1
}

.old-place {
  padding-top: 30px;
  opacity: 0
}

</style>
<template>
  <div>
    <div class="about-bg">
      <div class="ag-detail">
        <div class="ag-item">
          <div>
            <span class="ag-title">使命</span>
          </div>
          <div class="ag-label">用新科技突破传统商业模式，创造新价值</div>
        </div>
        <div class="ag-item">
          <div>
            <span class="ag-title">愿景</span>
          </div>
          <div class="ag-label">伴随合作伙伴一起成长，并能持久的企业</div>
        </div>
        <div class="ag-item">
          <div>
            <span class="ag-title">价值观</span>
          </div>
          <div class="ag-label">简单、能信赖</div>
        </div>
      </div>
    </div>
    <!-- 企业介绍 -->
    <div class="companyintro-title">
      <div class="">
        <span class="line"></span>
        <span class="txt">企 业 介 绍</span>
        <span class="line"></span>
      </div>
      <div class="companyintro-title-en">Enterprise introduction</div>
    </div>
    <div ref="about1" :class="[(flag['about1'] ? 'moveToTop' : 'old-place')]" class="companyintro-bg">
      <div style="padding-right: 15px;min-width: 550px;color: #797979;line-height: 30px;font-size: 16px;">
        <div></div>
        <div> 这是测试项目</div>
      </div>
      <div style="">
        <img style="width: 428px;height: 274px;
  " src="../assets/building.png">
      </div>
    </div>
    <!-- 发展历程 -->
    <div class="companyintro-title">
      <div class="">
        <span class="line"></span>
        <span class="txt">发 展 历 程</span>
        <span class="line"></span>
      </div>
      <div class="companyintro-title-en">Development history</div>
    </div>
    <div ref="about2" :class="[(flag['about2'] ? 'moveToTop' : 'old-place')]">
      <div class="developmentdetail">
        <div class="development-time" style="display: flex;">
          <div class="dp-item">2020年</div>
          <div style="border: 3px solid #D9003C;width: 10px; height: 10px;border-radius: 50%;margin-top: 3px;"></div>
        </div>
        <div ref="about3" :class="[(flag['about3'] ? 'moveToTop' : 'old-place')]">
          <div class="dp-title-item">
            <div class="dp-title-item-label"></div>
            <div class="dp-title-item-content"></div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import Vue from 'Vue'
export default {
  components: {},
  data() {
    return {
      viewHeight: 0,
      flag: {},
    }
  },
  watch: {

  },
  created() {

  },
  mounted: function() {

    var num = 4;
    //页面初始化
    this.pageInit('about', num);
    //页面滚动动画
    this.pageScrollAmi('about', num);
    //页面尺寸变化
    this.pageSize();
  },
  methods: {
    //页面初始化
    pageInit: function(name, num) {
      var _this = this;
      _this.viewHeight = this.getViewHeight();
      //初始化dom信息
      for (let i = 1; i < num; i++) {
        _this.flag['dom' + i] = _this.$refs[name + '' + i];
        let rectY = _this.flag['dom' + i].getBoundingClientRect().top;
        if (rectY < _this.viewHeight) {
          Vue.set(_this.flag, (name + '' + i), true);
        } else {
          Vue.set(_this.flag, (name + '' + i), false);
        }
        _this.$forceUpdate();
      }
    },
    //页面滚动动画
    pageScrollAmi: function(name, num) {
      var _this = this;
      window.onscroll = function() {
        for (let i = 1; i < num; i++) {
          let rectY = _this.flag['dom' + i].getBoundingClientRect().top;
          if (rectY < _this.viewHeight) {
            Vue.set(_this.flag, (name + '' + i), true);
          } else {
            Vue.set(_this.flag, (name + '' + i), false);
          }
          _this.$forceUpdate();
        }

        //页面滚动高度
        var scrollTop = document.documentElement.scrollTop || document.body.scrollTop;
        if (scrollTop > _this.$parent.scrollNum) {
          //向下滚动
          _this.$parent.hideFlag = true;
        } else {
          //向上滚动
          _this.$parent.hideFlag = false;
        }
        _this.$parent.scrollNum = scrollTop;
      }
    },
    //页面尺寸变化
    pageSize: function() {
      var _this = this;
      window.onresize = function() {
        _this.viewHeight = _this.getViewHeight();
      }
    },
    getViewHeight: function() {
      return document.documentElement.clientHeight || document.body.clientHeight
    }
  }
}

</script>



