<style type="text/css" scoped>
.joinus-bg {
  width: 100%;
  height: 637px;
  background: url(../assets/topbg.png) no-repeat center center;
  background-size: 100% 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}



.companyintro-title {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  margin-top: 60px;
  margin-bottom: 70px;
}

.companyintro-title .txt {
  color: black;
  width: 200px;
  font-size: 30px;
  padding-left: 10px;
  padding-right: 10px;
  height: 60px;
}

.companyintro-title-en {
  height: 24px;
  line-height: 24px;
  color: #5B5B5B;
  font-size: 20px;
}


.moveToTop {
  padding-top: 0;
  opacity: 1
}

.old-place {
  padding-top: 30px;
  opacity: 0
}

.cl-img {
  display: flex;
  width: 100%;
  transition: all 0.5s;
  flex-wrap: wrap;
  justify-content: center;
}

.cimg {
  width: 353px;
  height: 218px;
  margin: 5px;
  border-radius: 5px;
  background-size: 100% 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
  font-size: 26px;
  font-weight: 400;
}

.img1 {
  background: url(../assets/ai.png) no-repeat center center;
}

.img2 {
  background: url(../assets/blockchain.png) no-repeat center center;
}

.img3 {
  background: url(../assets/bigdata.png) no-repeat center center;
}

.img4 {
  background: url(../assets/cloud.png) no-repeat center center;
}

</style>
<template>
  <div style="margin-bottom: 100px;">
    <div class="joinus-bg">
    </div>
    <!-- 联系我们 -->
    <div class="companyintro-title">
      <div class="txt">vue网站</div>
      <div class="companyintro-title-en ">vue网站</div>
    </div>
    <div ref="about1" :class="(flag['about1'] ? 'moveToTop' : 'old-place')">
      <div class="cl-img">
        <div class="cimg img1">
          <div class="bl-lab">人工智能</div>
        </div>
        <div class="cimg img2">
          <div class="bl-lab">区块链技术</div>
        </div>
      </div>
      <div class="cl-img">
        <div class="cimg img3">
          <div class="bl-lab">大数据</div>
        </div>
        <div class="cimg img4">
          <div class="bl-lab" style="color: black;">云计算</div>
        </div>
      </div>
    </div>
  </div>
  </div>
  </div>
</template>
<script>
import Vue from 'Vue'
export default {
  components: {},
  data() {
    return {
      viewHeight: 0,
      flag: {},
    }
  },
  watch: {

  },
  created() {

  },
  mounted: function() {

    var num = 2;
    //页面初始化
    this.pageInit('about', num);
    //页面滚动动画
    this.pageScrollAmi('about', num);
    //页面尺寸变化
    this.pageSize();
  },
  methods: {
    //页面初始化
    pageInit: function(name, num) {
      var _this = this;
      _this.viewHeight = this.getViewHeight();
      //初始化dom信息
      for (let i = 1; i < num; i++) {
        _this.flag['dom' + i] = _this.$refs[name + '' + i];
        let rectY = _this.flag['dom' + i].getBoundingClientRect().top;
        if (rectY < _this.viewHeight) {
          Vue.set(_this.flag, (name + '' + i), true);
        } else {
          Vue.set(_this.flag, (name + '' + i), false);
        }
        _this.$forceUpdate();
      }
    },
    //页面滚动动画
    pageScrollAmi: function(name, num) {
      var _this = this;
      window.onscroll = function() {
        for (let i = 1; i < num; i++) {
          let rectY = _this.flag['dom' + i].getBoundingClientRect().top;
          if (rectY < _this.viewHeight) {
            Vue.set(_this.flag, (name + '' + i), true);
          } else {
            Vue.set(_this.flag, (name + '' + i), false);
          }
          _this.$forceUpdate();
        }

        //页面滚动高度
        var scrollTop = document.documentElement.scrollTop || document.body.scrollTop;
        if (scrollTop > _this.$parent.scrollNum) {
          //向下滚动
          _this.$parent.hideFlag = true;
        } else {
          //向上滚动
          _this.$parent.hideFlag = false;
        }
        _this.$parent.scrollNum = scrollTop;
      }
    },
    //页面尺寸变化
    pageSize: function() {
      var _this = this;
      window.onresize = function() {
        _this.viewHeight = _this.getViewHeight();
      }
    },
    getViewHeight: function() {
      return document.documentElement.clientHeight || document.body.clientHeight
    }
  }
}

</script>



