
import {
  Form,
  FormItem,
  Select,
  Button,
  Loading,
  Message,
  MessageBox,
  Menu,
  MenuItem,
  Tag,
  Pagination,
  Table,
  TableColumn,

} from 'element-ui'
const element = {
  install: function(Vue) {
      Vue.use(Form)
      Vue.use(FormItem)
      Vue.use(Button)
      Vue.use(Loading)
      Vue.use(Menu)
      Vue.use(MenuItem)
      Vue.use(Select)
      Vue.use(Tag)
      Vue.use(Pagination)
      Vue.use(Table)
      Vue.use(TableColumn)
      
      
      Vue.prototype.$message = Message;
      Vue.prototype.$confirm = MessageBox.confirm;
      Vue.prototype.$alert = MessageBox.alert;

  }
}

export default element


