<style scoped>
</style>
<template>
  <div >
    <!-- 页头 -->

    <appHeader :hide="hideFlag" :scroll="scrollNum"></appHeader>
    <!-- 正文页 -->
    <router-view></router-view>
    <!-- 页脚 -->
    <appFooter :theme="footTheme" ></appFooter>
  </div>
</template>
<script>
import appHeader from './header.vue'
import appFooter from './footer.vue'
export default {
  components: { appHeader, appFooter },
  data() {
    return {
      hideFlag: false, //页头是否隐藏
      scrollNum: 0, //页面滚动值

    }
  },
  watch: { //view层的全局监听
    "$route": function(to, from) {
      //自动回到页面顶部
      if (document.documentElement && document.documentElement.scrollTop) { // Explorer 6 Strict
        document.documentElement.scrollTop = 0;
      }
      if (document.body) { // all other Explorers
        document.body.scrollTop = 0;
      }
      //进入新闻详情页
      if (to.path == "/newsDetail") {
        this.hideFlag = false;
        this.scrollNum = 1;
        this.footTheme = "white";
      } else {
        this.scrollNum = 0;
        this.footTheme = "";
      }
    }
  },
  created: function() {


  },
  mounted: function() {
   
  },
  methods: {
    //监听页面滚动
    listenScroll: function() {
      var _this = this;
      window.onscroll = function() {
        //为了保证兼容性，这里取两个值，哪个有值取哪一个
        //scrollTop就是触发滚轮事件时滚轮的高度
        var scrollTop = document.documentElement.scrollTop || document.body.scrollTop;
        if (scrollTop > _this.scrollNum) {
          //向下滚动
          _this.hideFlag = true;
        } else {
          //向上滚动
          _this.hideFlag = false;
        }
        _this.scrollNum = scrollTop;
      }
    },
  }
}

</script>




