<style scoped>
.top-ver-line {
  z-index: 9;
  width: 2px;
  height: 20px;
  background-color: #ccc !important;
}

.head-bg {
  width: 100%;
  position: fixed;
  top: 0px;
  left: 0px;
  z-index: 3;
  transition: all 0.3s;
  height: 70px;
  overflow: hidden;
}

.head-w {
  position: absolute;
  width: 100%;
  height: 0;
  top: 0px;
  transition: all 0.3s;
  background-color: #fff;
}

.head-wbig {
  height: 70px;
}

.head-bgwsmall {

  box-shadow: 0 1px 6px 2px rgba(221, 221, 221, 0.24);
}

@keyframes toBig {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

.head-bgw {
  background-color: #fff;
  height: 70px;
}

.head-bg-hide {
  top: -70px;
}

.head-main {
  width: 1200px;
  margin: auto;
  display: flex;
  /*justify-content: space-between;*/
}

.logo-box {
  /*margin-left: 40px;*/
  width: 120px;
  height: 70px;
  background-size: 100%;
  cursor: pointer;
  z-index: 2;
  background-size: 100%;
}



.head-bgw .logo-box {

  background-size: 100%;
}

.head-bgwsmall .logo-box {

  background-size: 100%;
}


.menu-box {
  display: flex;
  align-items: center;
  justify-content: space-between;
  /* width: 600px; */
  height: 70px;
  font-size: 16px;
  color: #fff;
  margin-left: 140px;
}

.menu-box-w {
  width: 901px;
}

.head-bgw .menu-box {
  color: #333;
  z-index: 2;
}

.head-bgwsmall .menu-box {
  color: #333;
}

.child-box {
  position: absolute;
  top: 40px;
  cursor: pointer;
  opacity: 0;
  transition: all 0.3s;
}

.menu-item {
  cursor: pointer;
  height: 100%;
  display: flex;
  align-items: center;
  position: relative;
  padding: 0 10px;
  /*font-weight: bolder;*/
  justify-content: center;
  /* width: 80px; */
  margin: 0 40px;
}

.menu-item:after {
  content: "";
  position: absolute;
  left: 50%;
  bottom: 10px;
  height: 4px;
  width: 0px;
  background: #D9003C;
  transition: all 0.5s ease;
  -webkit-transition: all 0.3s ease;
}

.menu-item:hover:after {
  left: 10%;
  width: 80%;

}

.menu-item:hover {
  color: #D9003C;
}

.menu-act:after {
  left: 10%;
  width: 80%;

}
.menu-act {
  font-weight: 700;
}

.menu-act1 {
  font-weight: 700;
}
.menu-act1:after {
  left: 10%;
  width: 80%;
  background-color: #fff;
}

.menu-item:hover .child-box {
  opacity: 1;
  top: 70px;

}

.child-box div {
  color: #666;
  font-size: 18px;
  padding: 12px 0;
}

.child-box div:hover {
  color: #333;
}

.head-lang {
  border: 1px solid #fff;
  border-radius: 20px;
  width: 50px;
  height: 22px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 14px;
  cursor: pointer;
  z-index: 2;
}

.head-bgw .head-lang {
  border: 1px solid #333;
}

.head-bgwsmall .head-lang {
  border: 1px solid #333;
}

</style>
<template>
  <div class="head-bg" :class="{
      'head-bgw': wFlag,
      'head-bg-hide': false,
      'head-bgwsmall': curScroll != 0,
    }">
    <div class="head-w" :class="{ 'head-wbig': curScroll != 0 }"></div>
    <div class="head-main">
     
      <div class="menu-box" @mouseover="overItem($event)" @mouseleave="leaveItem($event)">
        <div class="menu-item" @click="goTo($event, 'home')" :class="{
            'menu-act': cur == 'home' && curScroll != 0 && !wFlag,
            'menu-act1': cur == 'home' && curScroll == 0 && !wFlag,
          }">
          {{ head.item1 }}
        </div>
        <div class="top-ver-line"></div>
        <div class="menu-item" @click="goTo($event, 'about')" :class="{
            'menu-act': cur == 'about' && curScroll != 0  && !wFlag,
            'menu-act1': cur == 'about' && curScroll == 0 && !wFlag,
          }">
          {{ head.item2 }}
        </div>
        <div class="top-ver-line"></div>
        <div class="menu-item" @click="goTo($event, 'businessIntro')" :class="{
            'menu-act': cur == 'businessIntro' && curScroll != 0  && !wFlag,
            'menu-act1': cur == 'businessIntro' && curScroll == 0 && !wFlag,
          }">
          {{ head.item3 }}
        </div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  components: {},
  props: {
    hide: "", //【非必传】，是否隐藏
    scroll: "", //页面滚动值
  },
  data() {
    return {
      wFlag: false, //菜单主题是否为白色
      hideFlag: false, //this.hide, //菜单是否隐藏
      curScroll: this.scroll, //页面滚动值
      cur: "home",
      head: {
        item1: '首页',
        item2: '关于我们',
        item3: '业务介绍'
      },

    };
  },

  watch: {
    hide: function(newVal, oldVal) {
      this.hideFlag = newVal;
    },
    scroll: function(newVal, oldVal) {
      this.curScroll = newVal;
    },
    $route(to, from) {

      var toPath = to.path;
      if (toPath == "/home") {
        this.cur = "home";
      } else if (toPath == "/about") {
        this.cur = "about";
      } else if (toPath == "/businessIntro") {
        this.cur = "businessIntro";
      } else if (toPath == "/joinus") {
        this.cur = "joinus";
      } else {
        this.cur = "contactus";
      }
    },
  },

  methods: {
    //鼠标移入菜单
    overItem: function(e) {
      this.wFlag = true;
    },
    //鼠标离开菜单
    leaveItem: function(e) {
      this.wFlag = false;
    },
    //跳转菜单
    goTo: function(e, path, type) {
      e.stopPropagation();
      var param = type ? { type: type } : {};
      this.$router.push({
        path: "/" + path,
        query: param,
      });
      this.wFlag = false;
      this.cur = path;

    },
    goToOut: function(t) {
      window.open(t, "_blank")
    }
  },
};

</script>



