import index from './components/index.vue'
//首页
import home from './components/home.vue'
import about from './components/aboutus.vue'
import businessIntro from './components/businessIntro.vue'



let routes = [{
  path: '/',
  component: index,
  name: '主页',
  children: [{
      path: "/",
      component: home,
      name: '首页',
    },
    {
      path: "/home",
      component: home,
      name: '首页',
    },
    {
      path: "/about",
      component: about,
      name: '关于我们',
    },
    {
      path: "/businessIntro",
      component: businessIntro,
      name: '业务介绍',
    }

  ]
},
{
  path: "/layout",
  component: () => import( './developmentPlatform/page/layout'),
  name: 'layout',
  children:[
    {
      path:'/register',
      component: () => import( './developmentPlatform/page/registerOrLogin/register'),
      name: 'register',
    },
    {
      path: '/createApp',
      component: () => import( './developmentPlatform/page/registerOrLogin/createApp'),
      name:'createApp'
    },
    {
      path: '/certification',
      component: () => import( './developmentPlatform/page/registerOrLogin/certification'),
      name:'certification'
    },
    {
      path: '/personalCertification',
      component: () => import( './developmentPlatform/page/registerOrLogin/personalCertification'),
      name:'personalCertification'
    },
    {
      path: '/companyCertification',
      component: () => import( './developmentPlatform/page/registerOrLogin/companyCertification'),
      name:'companyCertification'
    },
    {
      path: '/platformHome',
      component: () => import( './developmentPlatform/page/home'),
      name:'platformHome'
    },
    {
      path: '/apiList',
      component: () => import( './developmentPlatform/page/apiList'),
      name:'apiList'
    },
    {
      path: '/documents',
      component: () => import( './developmentPlatform/page/documents'),
      name:'documents'
    },
    {
      path: '/my',
      component: () => import( './developmentPlatform/page/my'),
      name:'my'
    },
    {
      path: '/apiDetail',
      component: () => import( './developmentPlatform/page/my/apiDetail'),
      name:'apiDetail'
    }
    
  ]
}
];

export default routes;



