<style type="text/css" scoped>
.foot-bg {
  width: 100%;
  height: 160px;
  background-color: #373737;
  padding-top: 20px;
}



.foot-main {
  /*width: 1200px;*/
  display: flex;
  justify-content: center;
}

.foot-top {
  display: flex;
  height: 120px;
  color: white;
  width: 1200px;
  display: flex;
  justify-content: space-between;
  align-items: center;


}

.foot-item {
  font-size: 16px;

}

.foot-item span {
  width: 2px;
  height: 18px;
  border-left: 1px solid white;
  margin-top: 3px;
}

.foot-bottom {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 40px;
  background-color: #000;
  color: #B7B1B1;
}

.foot-bottom-menu {
  cursor: pointer;
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
}

.bottom-menu-item {
  display: flex;
  justify-content: center;
  width: 100px;
  height: 19px;
  margin-bottom: 10px;


}

.foot-compamy {
  height: 26px;
}

</style>
<template>
  <div class="foot-bg">
    <div class="foot-main">
      <div class="foot-top">
        <div class="foot-item">
          这是footer
        </div>
        <div class="foot-item" style="padding-right: 177px;">
          <div class="foot-bottom-menu">
            <div @click="goTo('home')" class="bottom-menu-item">首页</div>
            <span></span>
            <div @click="goTo('about')" class="bottom-menu-item">关于我们</div>
            <span></span>
            <div @click="goTo('businessIntro')" class="bottom-menu-item">业务介绍</div>
          </div>
          <div class="foot-bottom-menu">
            <div @click="goTo('joinus')" class="bottom-menu-item">加入我们</div>
          </div>
        </div>
      </div>
    </div>
    <div class="foot-bottom">
      <div>
       githubwebsite
        <span style="">
          
            githubwebsite

        </span>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  components: {},
  props: {
    theme: "",
  },
  data() {
    return {
      curTheme: this.theme,
    }
  },
  watch: {

  },
  mounted: function() {},
  methods: {
    //页面跳转
    goTo: function(path, type) {
      var param = (type ? { type: type } : {});
      this.$router.push({
        path: '/' + path,
        query: param
      })
    }
  }
}

</script>



