<script>
import Vue from 'Vue'
import countTo from 'vue-count-to';
export default {
  components: { countTo },
  data() {
    return {
      videoSrc: "../../assets/topvideo.mp4",
      tabIndex: 0,
      flag: {},
      viewHeight: 0, //页面可视区域高度
      mediaTer: "", //终端类型
      offLang: {
        lang: 'cn'
      }

    }
  },
  created: function() {

  },
  mounted: function() {
    var num = (this.mediaTer == "Mobile" ? 7 : 7);
    //页面初始化
    this.pageInit('home', num);
    //页面滚动动画
    this.pageScrollAmi('home', num);
    //页面尺寸变化
    this.pageSize();
  },
  methods: {
    //页面初始化
    pageInit: function(name, num) {
      var _this = this;
      _this.viewHeight = this.getViewHeight();
      //初始化dom信息
      for (let i = 1; i < num; i++) {
        _this.flag['dom' + i] = _this.$refs[name + '' + i];
        if (_this.flag['dom' + i]) {
          let rectY = _this.flag['dom' + i].getBoundingClientRect().top;
          if (rectY < _this.viewHeight) {
            Vue.set(_this.flag, (name + '' + i), true);
          } else {
            Vue.set(_this.flag, (name + '' + i), false);
          }
          _this.$forceUpdate();
        }
      }
    },
    //页面滚动动画
    pageScrollAmi: function(name, num) {
      var _this = this;
      window.onscroll = function() {
        for (let i = 1; i < num; i++) {
          if (_this.flag['dom' + i]) {
            let rectY = _this.flag['dom' + i].getBoundingClientRect().top;
            if (rectY < _this.viewHeight) {
              Vue.set(_this.flag, (name + '' + i), true);
            } else {
              Vue.set(_this.flag, (name + '' + i), false);
            }
            _this.$forceUpdate();
          }
        }

        //页面滚动高度
        var scrollTop = document.documentElement.scrollTop || document.body.scrollTop;
        if (scrollTop > _this.$parent.scrollNum) {
          //向下滚动
          _this.$parent.hideFlag = true;
        } else {
          //向上滚动
          _this.$parent.hideFlag = false;
        }
        _this.$parent.scrollNum = scrollTop;
      }
    },
    //页面尺寸变化
    pageSize: function() {
      var _this = this;
      window.onresize = function() {
        _this.viewHeight = _this.getViewHeight();
      }
    },
    //跳转到业务系统介绍
    goTo: function(path, type) {
      this.$router.push({
        path: '/business',
        query: { type: type }
      })
    },
    getViewHeight: function() {
      return document.documentElement.clientHeight || document.body.clientHeight
    }
  }
}

</script>



